import cuModal from "@/meetu-ui/components/cu-modal.vue";
export default {
	name: 'homeIndex',
	components: {
		cuModal
	},
	data() {
		return {
			modalName: '',
			appName: 'Meet U',
			userInfo: {
				avatar: '../../static/logo.png',
				usenum: 4,
				total: 6
			},
			showSendToast: false,
		}
	},
	onLoad() {
		// uni.request({
		// 	url: 'https://api.meetu.letwx.com/v1/user/info' + '?token=hzPGaRoNhVlaIGsuhJmPbMv5vCccRPns',
		// 	data: {},
		// 	method: 'POST',
		// 	sslVerify: false,
		// 	success: (userres) => {
		// 		console.log('userres', userres);
		// 	},
		// 	fail:(error)=> {
		// 		console.log(error); 
		// 	}
		// })
		this.$http1.post('user/info', {}, {
			params: {
				token: 'hzPGaRoNhVlaIGsuhJmPbMv5vCccRPns'
			}
		}).then(res=>{
			console.log('userinfo-ok', res);
		}).catch(err=>{
			console.log('userinfo-err', err);
		})
	},
	onReady() {
		if(uni.getStorageSync('homeSendToast')) {
			this.showSendToast = false;
		} else {
			this.showSendToast = true;
		}
	},
	methods: {
		linkUser() {
			uni.navigateTo({
				url: '../user/index',
				animationDuration: 300,
				animationType: 'slide-in-bottom'
			})
		},
		linkAction(type) {
			switch (type){
				case 'search':
					uni.navigateTo({
						url: '../search/search',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'send':
					if(!uni.getStorageSync('homeSendToast')) {
						uni.setStorageSync('homeSendToast', true);
						this.showSendToast = false;
					} 
					this.modalName = 'bottomModal';
					break;
				case 'chatList':
					uni.navigateTo({
						url: '../chat/list',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'sendText':
					this.modalName = '';
					uni.navigateTo({
						url: '../send/text',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
				case 'sendVoice':
					this.modalName = '';
					uni.navigateTo({
						url: '../send/voice',
						animationDuration: 300,
						animationType: 'slide-in-bottom'
					})
					break;
			}
		},
		hideModal() {
			this.modalName = '';
		}
	}
}
